body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --background-color:  #2a329e;
  --background-inner-color: linear-gradient(180deg,#297af9,#333fb7);
  --text-color: whitesmoke;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    background-color: #2a329e !important;
    background-color: var(--background-color) !important;
    overflow: hidden;
}

#root {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
    font-family: system-ui;
    
}

.App {
    color: whitesmoke;
    color: var(--text-color);
}
.App 
.ui.dimmer {
    background-color: #2a329e;
    background-color: var(--background-color);
}

@media screen and (max-width: 768px) {
    .App 
    .ui.dimmer {
        background: linear-gradient(180deg,#297af9,#333fb7) !important;
        background: var(--background-inner-color) !important;
    }
}

#main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: linear-gradient(180deg,#297af9,#333fb7);
    background: var(--background-inner-color);
    border-radius: 10px;
    box-shadow: 0 3px 20px 0 rgb(0 0 0 / 6%);
    height: 667px;
    width: 375px;
}

@media screen and (max-width: 768px) {
    #main {
        height: 100vh;
        width: 100vw;
    }
}


.header 
.city {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 1rem;
}
.fa-location-dot {
    font-size: 1rem;
    margin-right: 10px;
}

#main 
.weather-icon 
img {
    height: 10rem;
}

#main 
.description {
    font-size: 1.3rem;
    font-weight: 800;
}

#main 
.temperature {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 0.4;
}

#main 
.temperature 
.temperature-value {
    font-size: 7rem;
    font-weight: 100;
    position: relative;
}

#main 
.temperature 
.temperature-value 
.degrees {
    position: absolute;
    top: -20px;
    right: -35px;
    font-size: 2rem;
}


#main 
.min-max-temperature {
    display: flex;
    justify-content: center;
    grid-gap: 3rem;
    gap: 3rem;
    flex-grow: 0.1;
    font-size: 1.3rem;
}


#main 
.humidity {
    font-size: 1.3rem;
}

.sun-position {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 10px;
    gap: 10px;
    flex-grow: 0.2;
}



